import React from "react";
import propTypes from "prop-types";
import {Link} from "react-router-dom";
import Input from "../../common/inputGroup";
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";

import {register} from "../../actions/authAction";

class Register extends React.Component{

  constructor(){
    super();
    this.state = {
      name: "",
      email: "",
      password: "",
      errors: {},
    }
    this.handleChange = this.handleChange.bind(this);
    this.register = this.register.bind(this);
  }


  componentDidMount(){
    console.log(this.props)
    if (this.props.auth.isAuthenticated) {
      this.props.history.push("/dashboard");
    }
  }

  componentWillReceiveProps(nextProps, nextContext) {
    if (nextProps.errors){
        this.setState({errors: nextProps.errors});
    }
}

  // componentDidUpdate(prevProps){
  //   if (this.props.errors !== prevProps.errors) {
  //     this.setState({errors: prevProps.errors});
  //   }
  // }

  handleChange(e){
    e.preventDefault();
    this.setState({[e.target.name] : e.target.value});
  }

  register(e){
    e.preventDefault();
    const newUser  = {
      name: this.state.name,
      email: this.state.email,
      password: this.state.password,
    }
    console.log(newUser);

    this.props.register(newUser, this.props.history);
  }

    render(){
      const {errors} = this.state;
        return(
            <div className="auth-bg">
            <div className="container d-flex flex-column">
  <div className="row align-items-center justify-content-center no-gutters min-vh-100">
    <div className="col-lg-5 col-md-8 py-8 py-xl-0">
      {/* Card */}
      <div className="card shadow">
        {/* Card body */}
        <div className="card-body p-6">
          <div className="mb-4">
            <Link to="/"><img src="../assets/images/Uma-Ukpai-Poly.png" className="mb-4 logo" alt /></Link>
            <h1 className="mb-1 font-weight-bold">Sign up</h1>
            <span>Already have an account?
              <Link to="/login" className="ml-1">Sign in</Link></span>
          </div>
          {/* Form */}
          <form onSubmit={this.register}>
            {/* Username */}
            <Input name="name" label="Full Name" error={errors.name} onChnage={this.handleChange} value={this.state.name} placeholder="Full Name"  required/>
            {/* Email */}
            <Input type="email" name="email" value={this.state.email} error={errors.email} onChnage={this.handleChange} label="Email" placeholder="Email address here" required />
            {/* Password */}
            <Input type="password" name="password" value={this.state.password} error={errors.password} onChnage={this.handleChange} placeholder="**************" label="Password" required />
            {/* Checkbox */}
            <div className="form-group">
              <div className="custom-control custom-checkbox">
                <input type="checkbox" className="custom-control-input" id="agreeCheck" />
                <label className="custom-control-label" htmlFor="agreeCheck"><span>I agree to the <a href="sign-up.html#!">Terms of
                      Service </a>and
                    <a href="sign-up.html#!">Privacy Policy.</a></span></label>
              </div>
            </div>
            <div>
              {/* Button */}
              <button type="submit" className="btn btn-primary btn-block">
                Create Free Account
              </button>
            </div>
            <hr className="my-4" />
            <div className="mt-4 text-center">
              {/*Facebook*/}
              <a href="sign-up.html#!" className="btn-social btn-social-outline btn-facebook">
                <i className="fab fa-facebook" />
              </a>
              {/*Twitter*/}
              <a href="sign-up.html#!" className="btn-social btn-social-outline btn-twitter">
                <i className="fab fa-twitter" />
              </a>
              {/*LinkedIn*/}
              <a href="sign-up.html#!" className="btn-social btn-social-outline btn-linkedin">
                <i className="fab fa-linkedin" />
              </a>
              {/*GitHub*/}
              <a href="sign-up.html#!" className="btn-social btn-social-outline btn-github">
                <i className="fab fa-github" />
              </a>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</div>
</div>

        )
    }
}

Register.propTypes ={
  auth: propTypes.object.isRequired,
  register: propTypes.func.isRequired,
  errors: propTypes.object.isRequired,
}

const mapStateToProps = (state) => ({
  auth: state.auth,
  errors: state.errors,
})

export default connect(mapStateToProps, {register}) ( withRouter(Register));