import axios from "axios";
import {GET_FEE, GET_FEES, GET_ERRORS, CLEAR_FEE} from "./types";

export const getFees = () => dispatch => {
    axios.get("/api/v1/fee/get/all")
    .then(res => dispatch({
        type: GET_FEES,
        payload: res.data,
    }))
    .catch(err => dispatch({
        type: GET_ERRORS,
        payload: err.response.data,
    }))
}

export const getFeeById = (id) => dispatch => {
    axios.get("/api/v1/get", {id: id})
    .then(res => dispatch({
        type: GET_FEE,
        payload: res.data,
    }))
    .catch(err => dispatch({
        type: GET_ERRORS,
        payload: err.response.data,
    }))
}