import React from "react";
import propTypes from "prop-types";
import {getFees} from "../../actions/feeAction";
import {connect} from "react-redux";


class Fees extends React.Component{
    constructor(){
        super();
        this.state = {
            fee: "",
            errors: "",
        }
    }

    componentDidMount(){
        this.props.getFees();
    }
    render(){
        const {fees, loading} = this.props.fees;
        console.log(fees);
        return(
            ( fees.map((item, key) => (
                <div className="col-xl-4 col-lg-4 col-md-12 col-12 mb-3 mb-lg-0">
                <div className="text-center">
                  {/* PayOut Chart */}
                  <div id="payoutChart" className="apex-charts" />
                  <h4 className="mb-1">{item.type}</h4>
                  <h5 className="mb-0 display-4 font-weight-bold">{item.amount}</h5>
                  <p className="px-4">Update your payout method in settings</p>
                  <a href="#" className="btn btn-primary">
                    Pay Now
                  </a>
                </div>
              </div>
            )))
        )
    }
    
}

Fees.propTypes = {
    fee: propTypes.object.isRequired,
    getFees: propTypes.func.isRequired,
}
const mapStateToProps = (state) => ({
    fees: state.fee,
})
export default connect(mapStateToProps, {getFees}) (Fees);