import {GET_FEE, GET_FEES, FEE_LOADING} from "../actions/types";

const initialState = {
    fee: null,
    fees: null,
    loading: false,
};

export default (state = initialState,  action) => {
    switch (action.type) {
        case FEE_LOADING:
            return{
                ...state,
                loading: true,
            };
            case GET_FEE:
            return{
                ...state,
                loading: false,
                fee: action.payload,
            };
            case GET_FEES: 
            return {
                ...state,
                laoding: false,
                fees: action.payload,
            }
    
        default:
            return state;
    }
}