import {
  BrowserRouter as Router,
  Route,
  Switch,
  withRouter,
} from "react-router-dom";
import React, { useState } from "react";
import { Provider } from "react-redux";
import store from "./store";
import jwtDecode from "jwt-decode";
import authToken from './utils/authToken';
import {logOutUser, setCurrentUser } from "./actions/authAction";

import "./App.css";
import Navigation from "./components/Navigation";
import Landing from "./components/Landing";
import AdmissionLeter from "./components/AdmissionLetter";
import Payment from "./components/fee/Payment";
import Footer from "./components/Footer";
import Login from "./components/auth/Login";
import Register from "./components/auth/Register";
import Library from "./components/pages/library";
import History from  "./components/pages/history";
import Faculties from  "./components/pages/facultie";
import Requirement from "./components/pages/requirement";
import CourseRegistration from "./components/pages/course-registration";
import Recover from "./components/auth/Recover";
import PageNotFount from "./components/404";
import PrivateRoute from "./common/privateRoute";
import Dashboard from "./components/dashboard"


if (localStorage.getItem("jwtToken")) {
  authToken(localStorage.getItem("jwtToken"));
  const decoded = jwtDecode(localStorage.getItem("jwtToken"));
  store.dispatch(setCurrentUser(decoded));
  const currentTime = Date.now() / 1000;

  if (decoded.exp < currentTime) {
      store.dispatch(logOutUser());
      window.location.href = '/login';
  }
}

const Main = withRouter(({ match, location }) => {
  return (
    <div>
      {location.pathname !== "/my-admission-letter/*" &&
        location.pathname !== "/login" &&
        location.pathname !== "/register" &&
        location.pathname !== "/recover-account" &&
        location.pathname !== "*" &&
        match.isExact !== false && <Navigation />}
      <Switch>
        <Route exact path="/" component={Landing} />
        <Route path="/my-admission-letter/:reg_no" component={AdmissionLeter} />
        <Route exact path="/fees" component={Payment} />
        <Route exact path="/login" component={Login} />
        <Route exact path="/register" component={Register} />
        <Route exact path="/history" component={History}/>
        <Route exact path="/recover-account" component={Recover} />
        <Route exact path="/library" component={Library}/>
        <Route exact path="/faculties" component={Faculties} />
        <Route exact path="/course-registration" component={CourseRegistration} />
        <Route exact path="/requirement" component={Requirement}/>
        <PrivateRoute exact path="/dashboard" component={Dashboard}/>
        <Route component={PageNotFount} />
      </Switch>
      {location.pathname !== "/my-admission-letter/*" &&
        location.pathname !== "/login" &&
        location.pathname !== "/register" &&
        location.pathname !== "/recover-account" &&
        location.pathname !== "*" &&
        match.isExact !== false && <Footer />}
    </div>
  );
});

function App() {
  return (
    <Provider store={store}>
      <Router>
        <div>
          <Main />
        </div>
      </Router>
    </Provider>
  );
}

export default App;
