import React from "react";
import propTypes from "prop-types";
import {Link} from "react-router-dom";
import Input from "../../common/inputGroup";
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";

import {login} from "../../actions/authAction";

class Login extends React.Component{
    constructor(){
        super();
        this.state = {
            email: "",
            password: "",
            errors: {},
        }
        this.handleChange = this.handleChange.bind(this);
        this.login = this.login.bind(this);
    }

    componentDidMount(){
      if (this.props.auth.isAuthenticated) {
        this.props.history.push("/dashboard");
      }
    }
    componentWillReceiveProps(nextProps){
      if (nextProps.auth.isAuthenticated) {
        this.props.history.push("/dashboard");
      }
      if (nextProps.errors) {
        this.setState({errors: nextProps.error});
      }
    }

    handleChange (e){
        e.preventDefault()
        this.setState({[e.target.name] : e.target.value});
    }

    login(e){
      e.preventDefault();
      const user = {
        email: this.state.email,
        password: this.state.password,
      }
      this.props.login(user);
    }
    render(){
      const {errors} = this.state;
        return(
            <div className="auth-bg">
            <div className="container d-flex flex-column">
  <div className="row align-items-center justify-content-center no-gutters min-vh-100">
    <div className="col-lg-5 col-md-8 py-8 py-xl-0">
      {/* Card */}
      <div className="card shadow ">
        {/* Card body */}
        <div className="card-body p-6">
          <div className="mb-4">
            <Link to="/"><img src="../assets/images/Uma-Ukpai-Poly.png" className="mb-4 logo" alt /></Link>
            <h1 className="mb-1 font-weight-bold">Sign in</h1>
            <span>Dont have an account? <Link to="/register" className="ml-1">Sign up</Link></span>
          </div>
          {/* Form */}
          <form onSubmit={this.login}>
            {/* Username */}
            <Input name="email" type="email" value={this.state.email} error={errors.email} onChnage={this.handleChange} label="Email address" placeholder="Email Address" />
            {/* Password */}
            <Input name="password" type="password" value={this.state.password} error={errors.password} onChnage={this.handleChange} label="Password" placeholder="**************" required />
            {/* Checkbox */}
            <div className="d-lg-flex justify-content-between align-items-center mb-4">
              <div className="custom-control custom-checkbox">
                <input type="checkbox" className="custom-control-input" id="rememberme" />
                <label className="custom-control-label " htmlFor="rememberme">Remember me</label>
              </div>
              <div>
                <Link to="/recover-account">Forgot your password?</Link>
              </div>
            </div>
            <div>
              {/* Button */}
              <button type="submit" className="btn btn-primary btn-block">Sign in</button>
            </div>
            <hr className="my-4" />
            <div className="mt-4 text-center">
              {/*Facebook*/}
              <a href="sign-in.html#!" className="btn-social btn-social-outline btn-facebook">
                <i className="fab fa-facebook" />
              </a>
              {/*Twitter*/}
              <a href="sign-in.html#!" className="btn-social btn-social-outline btn-twitter">
                <i className="fab fa-twitter" />
              </a>
              {/*LinkedIn*/}
              <a href="sign-in.html#!" className="btn-social btn-social-outline btn-linkedin">
                <i className="fab fa-linkedin" />
              </a>
              {/*GitHub*/}
              <a href="sign-in.html#!" className="btn-social btn-social-outline btn-github">
                <i className="fab fa-github" />
              </a>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</div>
</div>

        )
    }
}

Login.propTypes = {
  auth: propTypes.object.isRequired,
  errors: propTypes.object.isRequired,
  login: propTypes.func.isRequired,
}

const mapStateToProps = (state) => ({
  errors: state.errors,
  auth: state.auth
})
export default connect(mapStateToProps, {login})( withRouter(Login));