import React from "react";
import propTypes from "prop-types";
import {connect} from "react-redux";
import defaultAvatar from "../../img/default-avatar.png";
import {logOutUser} from "../../actions/authAction";

class ProfileHeader extends React.Component{
    constructor(props){
        super(props);
        this.logOut = this.logOut.bind(this);
    }

    logOut(e){
      e.preventDefault();
      this.props.logOutUser();
    }

    render(){
      const {user} = this.props.auth;
        return(
            <div className="row align-items-center">
  {/* User Info */}
  <div className="col-xl-12 col-lg-12 col-md-12 col-12">
    <div className="pt-16 rounded-top" style={{background: 'url(../assets/images/profile-bg.jpg) no-repeat', backgroundSize: 'cover'}} />
    <div className="d-flex align-items-end justify-content-between bg-white px-4 pt-2 pb-4 rounded-none rounded-bottom shadow-sm">
      <div className="d-flex align-items-center">
        <div className="mr-2 position-relative d-flex justify-content-end align-items-end mt-n5">
          <img src={defaultAvatar} className="avatar-xl rounded-circle border-width-4 border-white" alt />
          <a href="#!" className="position-absolute mb-6 mr-n2" data-toggle="tooltip" data-placement="top" title data-original-title="Verified">
            <img src="../assets/images/checked-mark.svg" alt height={30} width={30} />
          </a>
        </div>
        <div className="lh-1">
          <h2 className="mb-0">{user.name}</h2>
          <p className="mb-0 d-block">{user.email}</p>
        </div>
      </div>
      <div>
        <button className="btn btn-primary btn-sm d-none d-md-block" onClick={this.logOut}>Logout</button>
      </div>
    </div>
  </div>
</div>


        )
    }
}

ProfileHeader.propTypes = {
  auth: propTypes.object.isRequired,
  user: propTypes.object.isRequired,
  errors: propTypes.object.isRequired,
  logOutUser: propTypes.func.isRequired,
}


const mapStateToProps = (state) => ({
  auth: state.auth,
  user: state.user,
  errors: state.errors,
})


export default  connect(mapStateToProps, {logOutUser})(ProfileHeader);