import axios from "axios";
import {CLEAR_CURRENT_STUDENT, GET_ERRORS, GET_STUDENT, GET_STUDENTS} from "./types";

export const checkAdmission = (regNo) => dispatch => {
    axios.post(`/api/v1/admission/letter`, {regNo: regNo})
    .then(res => dispatch({
        type: GET_STUDENT,
        payload: res.data,
    }))
    .catch(err => dispatch({
        type: GET_ERRORS,
        payload: err.response.data,
    }))
}

export const getAllStudents = () => dispatch => {
    axios.get("/admision/students")
    .then(res => dispatch({
        type: GET_STUDENTS,
        payload: res.data,
    }))
    .catch(err => dispatch({
        type: GET_ERRORS,
        payload: err.response.data,
    }))
}

export const getStudentByRegNo = (regNo) => dispatch => {
    axios.get(`/admision/student/${regNo}`)
    .then(res => dispatch({
        type: GET_STUDENT,
        payload: res.data,
    }))
    .catch(err => dispatch({
        type: GET_ERRORS,
        payload: err.response.data,
    }))
}   