import {GET_STUDENT, STUDENT_LOADING, CLEAR_CURRENT_STUDENT, GET_STUDENTS } from "../actions/types";

const initialState = {
    student: null,
    students: null,
    laoding: null,
};

export default (state = initialState, action) => {
    switch (action.type) {
        case STUDENT_LOADING:
           return{
               ...state,
               loading: true
           }
           case GET_STUDENT:
           return{
               ...state,
               loading: false,
               student: action.payload,
           }
           case GET_STUDENTS:
           return{
               ...state,
               laoding: false,
               students: action.payload,
           }
           case CLEAR_CURRENT_STUDENT:
           return{
               ...state,
               student: null,
               loading: false,
           }
    
        default:
           return state;
    }
}